@use '../../utils' as *;

/*----------------------------------------*/
/*  7.14 Testimonial css
/*----------------------------------------*/

.testimonial {
    &__thumb {
        position: relative;
        margin: 15px;
        @media #{$lg} {
            max-width: 400px;
        }
        img {
            border-radius: 50%;
            width: 100%;
        }
        &-wrap{
            &.style-four{
                margin-right: 40px;
                @media #{$lg,$md,$sm,$xs}{
                    margin-right: 0;
                }
                .testimonial{
                    &__thumb{
                        position: relative;
                        margin: 14px;
                        img{
                            width: 100%;
                        }
                        &::before{
                            position: absolute;
                            content: "";
                            position: absolute;
                            content: "";
                            border-radius: 50%;
                            background: linear-gradient(180deg, rgba(66, 134, 244, 0.60) 0%, rgba(66, 134, 244, 0.00) 100%);
                            width: calc(100% + 25px);
                            height: calc(100% + 25px);
                            z-index: -1;
                            left: -13px;
                            top: -13px;
                        }
                    }
                }
            }
        }
    }
    &__thumb-small {
        img{
            max-width: 78px;
            @media #{$md}{
                max-width: 100px;
            }
            @media #{$sm}{
                max-width: 78px;
            }
        }
        @media #{$xxs} {
            display: none;
        }
        // small img
        .img-one {
            position: absolute;
            left: 10px;
            z-index: 1;
            top: 50px;
            animation: zoom 5s infinite;
        }
        .img-two {
            position: absolute;
            right: 10px;
            z-index: 1;
            top: 50px;
            animation: zoom 5.1s infinite;
        }
        .img-three {
            position: absolute;
            bottom: -20px;
            left: 115px;
            z-index: 1;
            animation: zoom 5.2s infinite;
        }
    }
    &__bg {
        &:before {
            position: absolute;
            width: calc(100% + 20px);
            height: calc(100% + 20px);
            content: "";
            z-index: 1;
            left: -10px;
            right: 0;
            top: -10px;
            border-radius: 50%;
            border: 2px solid var(--bd-theme-primary);
            opacity: .2;
        }
        &::after {
            background: var(--bd-theme-primary);
            border-radius: 50%;
            width: calc(100% - 200px);
            height: calc(100% - 200px);
            position: relative;
            filter: blur(200px);
            position: absolute;
            top: 20%;
            left: 20%;
            z-index: -1;
            content: "";
        }
    }
    &__item{
        margin-left: 15px;
        @media #{$md,$sm,$xs}{
            margin-left: 0;
        }
        &.style-four{
            margin-right: 57px;
            margin-left: 0;
            @media #{$lg,$md,$sm,$xs}{
                margin-right: 0;
            }
            .testimonial{
                &__text{
                    p{
                        margin-bottom: 25px;
                    }
                }
            }
            .bd-swiper-dot{
                .swiper-pagination-bullet{
                    width: 13px;
                    height: 13px;
                }
            }
        }
    }
    &__text {
        p {
            font-size: 25px;
            line-height: 1.4;
            color: var(--bd-rgba-3);
            font-weight: 400;
            margin-bottom: 35px;
            letter-spacing: -0.56px;
            @media #{$md} {
                font-size: 23px;
            }
            @media #{$sm,$xs} {
                font-size: 20px;
            }
        }
    }
    &__rating {
        margin-bottom: 15px;

        a {
            color: #EAAF18;
            font-size: 18px;
        }
    }
    &__avatar {
        &-thumb{
            max-width: 64px;
        }
        &-item {
            display: flex;
            align-items: center;
            gap: 0 15px;
        }
        &-thumb {
            img {
                border-radius: 50%;
            }
        }
        &-title {
            line-height: 1.3;
            color: var(--bd-text-tertiary);
        }
        &-designation {
            font-size: 14px;
            letter-spacing: -0.28px;
            color: var(--bd-text-grey);
        }
    }
    &__navigation {
        position: absolute;
        bottom: 0;
        right: 0;
        z-index: 1;
    }
    &__wrapper {
        &.style-two {
            position: relative;
            margin-top: 20px;
            &::before {
                position: absolute;
                width: calc(100% - 120px);
                height: 100%;
                background: var(--bd-bg-secondary);
                content: "";
                right: -30px;
                border-radius: 8px;
                @media #{$sm,$xs} {
                    content: none;
                }
            }
            .testimonial {
                &__thumb {
                    max-width: 100%;
                    margin: 0;
                    @media #{$lg} {
                        position: relative;
                        bottom: -15px;
                    }
                    @media #{$md} {
                        position: relative;
                        bottom: -5px;
                    }
                    &::before {
                        position: absolute;
                        width: 100%;
                        height: 120px;
                        content: "";
                        background: linear-gradient(0deg, rgba(18, 19, 20, 1) 0%, rgba(18, 19, 20, 0) 100%);
                        bottom: 0;
                    }
                    img {
                        border-radius: 0;
                        width: 100%;
                    }
                }
                &__shape-dot {
                    position: absolute;
                    bottom: 50px;
                    z-index: -1;
                    animation: upDown 1.6s ease-in-out 1.6s forwards infinite alternate;
                }
                &__item {
                    position: relative;
                    max-width: 790px;
                    margin-left: 40px;
                    margin-bottom: 40px;
                    @media #{$lg} {
                        max-width: 100%;
                        margin-left: 10px;
                    }
                    @media #{$md} {
                        max-width: 100%;
                        margin-left: 0;
                    }
                    @media #{$sm,$xs} {
                        max-width: 100%;
                        margin-left: 0;
                    }
                    @media #{$xs} {
                        margin-bottom: 0;
                    }
                }
                &__shape-icon {
                    margin-bottom: 28px;
                }
                &__text {
                    margin-bottom: 30px;
                    h4 {
                        font-weight: 400;
                        line-height: 1.4;
                        color: var(--bd-rgba-3);
                    }
                }
                // &__navigation {
                //     bottom: -30px;
                //     @media #{$lg,$md}{
                //         bottom: 0;
                //     }
                // }
            }
        }
        &.style-three {
            position: relative;
            .testimonial {
                &__thumb {
                    background: var(--bd-bg-tertiary);
                    text-align: center;
                    border-radius: 14px;
                    position: relative;
                    margin: 0;
                    max-width: 100%;
                    margin-bottom: 25px;
                    &::before {
                        position: absolute;
                        content: "";
                        z-index: 5;
                        position: absolute;
                        width: calc(100% + 8px);
                        height: 100%;
                        content: "";
                        position: absolute;
                        left: 0;
                        top: 0;
                        border-radius: 14px;
                        border: 8px solid transparent;
                        background: var(--bd-gradient-6) border-box;
                        mask-mode: match-source, match-source;
                        mask-repeat: repeat, repeat;
                        mask-clip: content-box, border-box;
                        mask-origin: content-box, border-box;
                        mask-position: 0% 0%, 0% 0%;
                        mask-size: auto, auto;
                        -webkit-mask: var(--bd-gradient-6) content-box content-box, var(--bd-gradient-6);
                        -webkit-mask-composite: destination-out;
                        mask-image: var(--bd-gradient-6), var(--bd-gradient-6);
                        mask-composite: exclude;
                    }
                    img {
                        border-radius: 0;
                        width: unset;
                    }
                    &-wrap {
                        margin-right: 20px;
                        @media #{$sm,$xs}{
                            margin-right: 10px;
                        }
                        @media #{$sm}{
                            margin-right: 0;
                        }
                    }
                }
                &__rating {
                    margin: 0;
                }
                &__avatar-info {
                    text-align: center;
                    @media #{$sm,$xs}{
                        margin-bottom: 20px;
                    }
                }
                &__item {
                    margin-top: 55px;
                    margin-left: 30px;
                    margin-right: 135px;
                    position: relative;
                    @media #{$lg}{
                        margin-left: 0;
                    }
                    @media #{$md}{
                        margin-top: 15px;
                        margin-left: 0;
                        margin-right: 0;
                    }
                    @media #{$sm}{
                        margin-top: 20px;
                        margin-right: 0;
                        margin-left: 15px;
                    }
                    @media #{$xs}{
                        margin: 0;
                    }
                }
                &__shape-quote {
                    position: absolute;
                    right: -90px;
                    top: -55px;
                    max-width: 100px;
                    @media #{$sm,$md}{
                        display: none;
                    }
                    @media #{$xs}{
                        right: 0;
                        top: -70px;
                        max-width: 60px;
                    }
                    @media #{$xxs}{
                        right: 0;
                        top: -30px;
                        max-width: 40px;
                    }
                }
                &__navigation{
                    left: 50%;
                    transform: translateX(-50%);
                    right: auto;
                    @media #{$md}{
                        bottom: 2px;
                    }
                    @media #{$sm}{
                        transform: translateX(0%);
                        bottom: 20px;
                    }
                }
                &__text{
                    p{
                        margin: 0;
                    }
                }
            }
        }
        &.style-five{
            padding: 40px 30px 30px 30px;
            border: 2px solid var(--bd-border-primary);
            border-radius: 8px;
            transition: .3s;
            @media #{$md}{
                padding: 30px 30px 30px 30px;
            }
            @media #{$xxs}{
                padding: 30px 25px 30px 25px;
            }
            &:hover{
                background: #28282c;
                border: 2px solid transparent;
            }
            .testimonial{
                &__top-item{
                    display: flex;
                    align-items: center;
                    justify-content: space-between;
                    margin-bottom: 20px;
                    @media #{$md,$xxs}{
                        flex-wrap: wrap;
                        margin-bottom: 10px;
                        column-gap: 10px;
                    }
                }
                &__rating{
                    margin-bottom: 0;
                }
                &__item{
                    margin-left: 0;
                }
                &__thumb{
                    position: relative;
                    max-width: 90px;
                    margin: 5px;
                    z-index: 1;
                    // @media #{$xl,$lg}{
                    //     max-width: 100px;
                    // }
                    // @media #{$lg}{
                    //     max-width: 90px;
                    // }
                    @media #{$md,$sm}{
                        max-width: 70px;
                    }
                    @media #{$xs}{
                        max-width: 60px;
                    }
                    &::before{
                        position: absolute;
                        content: "";
                        border-radius: 50%;
                        background: var(--bd-theme-primary);
                        width: calc(100% + 10px);
                        height: calc(100% + 10px);
                        z-index: -1;
                        left: -5px;
                        top: -5px;
                    }
                    &-wrap{
                        display: flex;
                        align-items: center;
                        gap: 20px;
                        @media #{$md}{
                            margin-bottom: 20px;
                        }
                        @media #{$xxs}{
                            margin-bottom: 15px;
                            flex-wrap: wrap;
                            gap: 10px;
                        }
                    }
                }
                &__text{
                    p{
                        margin: 0;
                    }
                }
                &__shape{
                    @media #{$xl}{
                        max-width: 70px;
                    }
                    @media #{$lg}{
                        max-width: 60px;
                    }
                    @media #{$md,$sm,$xs}{
                        max-width: 50px;
                    }
                }
                &__navigation{
                    position: unset;
                    text-align: center;
                    margin-top: 40px;
                }
            }
        }
        &.style-six{
            text-align: center;
            position: relative;
            .testimonial{
                &__item{
                    background: var(--bd-bg-quaternary);
                    padding: 80px 70px 40px;
                    border-radius: 30px;
                    margin-left: 0;
                    position: relative;
                    margin: 15px 15px;
                    @media #{$xs}{
                        padding: 40px;
                    }
                    @media #{$xxs}{
                        padding: 30px;
                    }
                    &::before{
                        position: absolute;
                        content: "";
                        width: 210px;
                        height: 215px;
                        background: var(--bd-bg-quaternary);
                        bottom: -214px;
                        left: 70px;
                        clip-path: polygon(0% 0%, 100% 0%, 100% 0, 34% 0, 9% 27%, 8% 0, 0 0);
                        z-index: 2;
                    }
                    &::after{
                        position: absolute;
                        content: "";
                        width: calc(100% + 30px);
                        height: calc(100% + 30px);
                        left: -15px;
                        top: -15px;
                        border-radius: 30px;
                        border: 2px solid transparent;
                        background: var(--bd-gradient-6) border-box;
                        mask-mode: match-source, match-source;
                        mask-repeat: repeat, repeat;
                        mask-clip: content-box, border-box;
                        mask-origin: content-box, border-box;
                        mask-position: 0% 0%, 0% 0%;
                        mask-size: auto, auto;
                        -webkit-mask: var(--bd-gradient-6) content-box content-box, var(--bd-gradient-6);
                        -webkit-mask-composite: destination-out;
                        mask-image: var(--bd-gradient-6), var(--bd-gradient-6);
                        mask-composite: exclude;
                        z-index: -1;
                    }
                    .bg-shape{
                        position: absolute;
                        width: 177px;
                        height: 50px;
                        content: "";
                        background: var(--bd-bg-secondary);;
                        bottom: -50px;
                        z-index: 1;
                        left: 20px;
                    }
                }
                &__avatar{
                    &-item{
                        margin-top: 75px;
                        margin-left: 60px;
                    }
                    &-thumb{
                        position: relative;
                        &::before{
                            position: absolute;
                            content: "";
                            height: calc(100% + 4px);
                            width: calc(100% + 4px);
                            left: -3px;
                            top: -3px;
                            border-radius:50%;
                            border: 4px solid transparent;
                            background: var(--bd-gradient-6) border-box;
                            mask-mode: match-source, match-source;
                            mask-repeat: repeat, repeat;
                            mask-clip: content-box, border-box;
                            mask-origin: content-box, border-box;
                            mask-position: 0% 0%, 0% 0%;
                            mask-size: auto, auto;
                            -webkit-mask: var(--bd-gradient-6) content-box content-box, var(--bd-gradient-6);
                            -webkit-mask-composite: destination-out;
                            mask-image: var(--bd-gradient-6), var(--bd-gradient-6);
                            mask-composite: exclude;
                            z-index: 1;
                        }
                    }
                    &-title{
                        text-align: start;
                    }
                }
                &__text{
                    p{
                        margin-bottom: 0;
                    }
                }
                &__shape{
                    @media #{$xs}{
                        display: none;
                    }
                    &-one{
                        position: absolute;
                        top: 50px;
                        left: 15px;
                        max-width: 50px;

                    }
                    &-two{
                        position: absolute;
                        right: 15px;
                        top: 50px;
                        max-width: 50px;
                    }
                }
            }
        }
        &.style-seven{
            padding: 40px 30px 40px 30px;
            border-radius: 8px;
            transition: .3s;
            background: rgba(42, 42, 42, 0.60);
            backdrop-filter: blur(49px);
            @media #{$md}{
                padding: 30px 30px 20px 30px;
            }
            @media #{$xxs}{
                padding: 30px 25px 20px 25px;
            }
            .testimonial{
                &__top-item{
                    display: flex;
                    align-items: center;
                    justify-content: space-between;
                    margin-bottom: 20px;
                    @media #{$md,$xxs}{
                        flex-wrap: wrap;
                        margin-bottom: 10px;
                        column-gap: 10px;
                    }
                }
                &__rating{
                    margin-bottom: 0;
                }
                &__item{
                    margin-left: 0;
                }
                &__thumb{
                    position: relative;
                    max-width: 70px;
                    margin:0;
                    z-index: 1;
                    &-wrap{
                        display: flex;
                        align-items: center;
                        gap: 20px;
                        @media #{$md}{
                            margin-bottom: 20px;
                        }
                        @media #{$xxs}{
                            margin-bottom: 20px;
                            flex-wrap: wrap;
                            gap: 10px;
                        }
                    }
                }
                &__text{
                    p{
                        margin: 0;
                        margin-bottom: 30px;
                    }
                }
                &__shape{
                    @media #{$xl}{
                        max-width: 70px;
                    }
                    @media #{$lg}{
                        max-width: 60px;
                    }
                    @media #{$md,$sm,$xs}{
                        max-width: 50px;
                    }
                }
                &__navigation{
                    position: unset;
                    text-align: center;
                    margin-top: 40px;
                }
            }
        }
    }
    &__slider{
        .testimonial{
            &__navigation{
                text-align: center;
                position: unset;
                margin-top: 50px;
            }
        }
        &.style-two{
            position: relative;
            &:hover{
                .testimonial__navigation{
                    button{
                        opacity: 1;
                    }
                }
            }
            .testimonial{
                &__navigation{
                    position: unset;
                    margin: 0;
                    button{
                        margin-top: 0;
                        position: absolute;
                        top: 40%;
                        bottom: unset;
                        transform: translateY(-50%);
                        left: 5%;
                        opacity: 0;
                        &.testimonial__button-next{
                            right: 5%;
                            left: auto;
                        }
                    }
                }
            }
        }
    }
    &__shape{
        &-three{
            position: absolute;
            left: 135px;
            bottom: 160px;
            -moz-animation: rotate-infinite 3s ease-in-out 1s forwards infinite alternate;
            -webkit-animation: rotate-infinite 3s ease-in-out 1s forwards infinite alternate;
            animation: rotate-infinite 3s ease-in-out 1s forwards infinite alternate;
            @media #{$xl,$lg}{
                left: 55px;
                bottom: 70px;
            }
            @media #{$md}{
                left: 50px;
                bottom: 70px;
            }
            @media #{$sm}{
                left: 40px;
                bottom: 50px;
            }
            @media #{$xs}{
                display: none;
            }
        }
        &-four{
            position: absolute;
            right: 135px;
            top: 90px;
            -moz-animation: rotate-infinite 3s ease-in-out 1s forwards infinite alternate;
            -webkit-animation: rotate-infinite 3s ease-in-out 1s forwards infinite alternate;
            animation: rotate-infinite 3s ease-in-out 1s forwards infinite alternate;
            @media #{$xs}{
                display: none;
            }
        }
    }
}

// overlay
.bd-testimonial__area{
    &.style-seven{
        .is-overlay{
            &::before{
                position:absolute;
                content: "";
                width: 100%;
                height: 100%;
                opacity: 0.6;
                background: #131313;
                z-index: 1;
                }
        }
        .include-bg{
            @media #{$xl,$lg,$md,$sm,$xs}{
                background-position: right;
            }
        }
    }
}