@use '../../utils' as *;
/*----------------------------------------*/
/*  3.1 banner Style 1
/*----------------------------------------*/

// Banner style
.banner {
	&-height {
		padding-top: 90px;
		min-height: 900px;
		// padding-bottom: 40px;
		@media #{$xl,$lg} {
			min-height: 750px;
		}
		@media #{$xs, $sm, $md} {
			padding-top: 150px;
		}
		@media #{$xs} {
			min-height: 100%;
		}
		@media #{$xxs} {
			padding-top: 130px;
		}
		@media #{$md} {
			padding-bottom: 80px;
		}
		@media #{$sm,$xs} {
			padding-bottom: 65px;
		}
		&.style-two {
			padding-bottom: 0;
		}
		&.style-three {
			padding-top: 0;
			padding-bottom: 0;
			min-height: 940px;
			@media #{$xl} {
				min-height: 750px;
			}
			@media #{$lg} {
				min-height: 700px;
			}
			@media #{$md}{
				min-height: 650px;
			}
			@media #{$sm,$xs}{
				min-height: 600px;
			}
		}
		&.style-four {
			padding-top: 0;
			min-height: 840px;
			padding-bottom: 0;
			@media #{$xxl}{
				min-height: 790px;
			}
			@media #{$xl}{
				min-height: 680px;
			}
			@media #{$lg}{
				min-height: 600px;
			}
			@media #{$md}{
				min-height: 550px;
			}
			@media #{$sm,$xs}{
				min-height: 530px;
			}

		}
		&.style-five {
			min-height: 940px;
			@media #{$xxl} {
				min-height: 850px;
			}
			@media #{$xl} {
				min-height: 750px;
			}
			@media #{$lg} {
				min-height: 700px;
			}
			@media #{$md,$sm,$xs} {
				padding-bottom: 100px;
			}

		}
		&.style-six {
			padding: 130px 0 130px;
			min-height: 830px;
			z-index: 0;
			@media #{$xl}{
				padding: 100px 0 100px;
				min-height: 750px;
			}
			@media #{$lg}{
				padding: 100px 0 100px;
				min-height: 700px;
			}
			@media #{$md}{
				padding: 80px 0 0px;
				min-height: 600px;
			}
			@media #{$sm,$xs}{
				padding: 65px 0 0px;
				min-height: 500px;
			}

		}
		&.style-seven {
			min-height: 940px;
			padding-bottom: 0;
			padding-top: 0;
			@media #{$xxl}{
				min-height: 800px;
			}
			@media #{$xl}{
				min-height: 700px;
			}
			@media #{$lg}{
			min-height: 650px;
		}
			@media #{$md,$sm}{
			min-height: 600px;
		}
			@media #{$xs}{
			min-height: 550px;
		}

		}
		&.style-eight {
			min-height: 940px;
			padding-bottom: 0;
			@media #{$xxl}{
				min-height: 800px;
			}
			@media #{$xl}{
				min-height: 750px;
			}
			@media #{$lg}{
				min-height: 700px;
			}
		}
	}
	&__shape-two {
		position: absolute;
		left: 150px;
		bottom: 22%;
 		 animation: upDown 1.5s ease-in-out 1.5s forwards infinite alternate;
		@media #{$xxl,$xl,$lg} {
			left: 40%;
			bottom: 25%;
		}
	}
	&__content {
		padding-right: 100px;
		@media #{$xl,$lg} {
			padding-right: 50px;
		}
		@media #{$md,$sm,$xs} {
			padding-right: 0;
		}
		p {
			margin-bottom: 42px;
		}
		// &.style-three {
		// 	padding-right: 0;
		// 	p {
		// 		max-width: 518px;
		// 		margin: 0 auto;
		// 	}
		// }
	}
	&-overlay {
		position: relative;
	}
	&__thumb {
		&-wrapper {
			position: relative;
			// @media #{$md} {
			// 	max-width: 500px;
			// 	margin: 0 auto;
			// }
			@media #{$xxl,$xl} {
				margin-right: 70px;
			}
			.banner__thumb {
				img {
					border-radius: 615px;
					width: 100%;
				}
			}
			.banner__bg {
				&::before {
					position: absolute;
					content: "";
					width: 100%;
					height: calc(100% - 85px);
					border-radius: 100%;
					background-color: var(--bd-theme-primary);
					left: 0;
					bottom: 0;
					z-index: -1;
					opacity: .2;
					@media #{$xl} {
						height: calc(100% - 75px);
					}
					@media #{$lg} {
						height: calc(100% - 70px);
					}
					@media #{$xxs} {
						height: calc(100% - 50px);
					}
				}
				&::after {
					position: absolute;
					content: "";
					width: 400px;
					height: 400px;
					border-radius: 615.094px;
					background-color: var(--bd-theme-primary);
					left: 16%;
					z-index: -1;
					top: 25%;
					filter: blur(200px);
					animation: zoom 5s infinite;
					@media #{$xxl}{
						width: 350px;
						height: 350px;
						left: 19%;
					}
					@media #{$xl}{
						width: 300px;
						height: 300px;
						left: 19%;
					}
					@media #{$lg}{
						width: 250px;
						height: 250px;
						left: 22%;
					}
					@media #{$sm}{
						width: 250px;
						height: 250px;
						left: 25%;
						top: 30%;
					}
					@media #{$xs}{
						width: 250px;
						height: 250px;
						left: 26%;

					}
					@media #{$xxs}{
						width: 170px;
						height: 170px;
						left: 27%;
						top: 27%;

					}
				}
			}
			.banner__shape-one {
				position: absolute;
				left: 50px;
				top: 21%;
				animation: leftright 2s ease-in-out 2s forwards infinite alternate;
				@media #{$xs} {
					display: none;
				}
			}
		}
	}
	&__title {
		// line-height: 1.1;
		margin-bottom: 20px;
		letter-spacing: -1.28px;
	}
	&__shape-hand {
		position: relative;
		top: -9px;
		img{
			animation: bdShake 1.5s infinite;
		}
	}
	&__area {
		&.style-two {
			.banner {
				&__content {
					padding-right: 0;
					margin-right: -30px;
					@media #{$xs} {
						margin-right: 0;
					}
				}
				&__thumb {
					min-width: 900px;
					@media #{$xl} {
						min-width: 750px;
					}
					@media #{$lg} {
						min-width: 700px;
					}
					// @media #{$md} {
					// 	min-width: 600px;
					// }
					@media #{$md,$sm,$xs} {
						min-width: 100%;
					}
					@media #{$xl} {
						transform: translateX(-15%);
					}
					@media #{$lg} {
						transform: translateX(-20%);
					}
					img {
						border-radius: 0;
					}
					&-wrapper {
						padding-left: 40px;
						margin: 0;
						@media #{$md,$sm,$xs} {
							padding-left: 0px;
						}
					}
				}
				&__bg {
					&::before {
						content: none;
					}
					&::after {
						position: absolute;
						content: "";
						width: 290px;
						height: 290px;
						border-radius: 290px;
						background-color: var(--bd-theme-primary);
						left: 45%;
						z-index: -1;
						top: 45%;
						filter: blur(200px);
						opacity: 0.5;
					}
				}
			}
		}
		&.style-three{
			.is-overlay{
				clip-path: polygon(100% 0%, 110% 83%, 50% 100%, 0 86%, 0 0%);
				&::before{
					position:absolute;
					content: "";
					width: 100%;
					height: 100%;
					background:rgba(11, 11, 11, 0.75);
					z-index: 1;
					clip-path: polygon(100% 0%, 110% 83%, 50% 100%, 0 86%, 0 0%);
				}
			}
			.banner{
				&__content{
					padding-right: 0;
					p {
						max-width: 518px;
						margin: 0 auto 42px;
						@media #{$xxs}{
							max-width: 100%;
						}
					}
				}
				// &__btn{
				// 	&-wrap{
				// 		display: flex;
				// 		align-items: center;
				// 		justify-content: center;
				// 		gap: 20px 30px;
				// 	}
				// }
			}
		}
		&.style-four{
			.is-overlay{
				&::before{
					position:absolute;
					content: "";
					width: 100%;
					height: 100%;
					opacity: 0.55;
					background: #000;
					z-index: 1;
				}
			}
			.banner{
				&__content{
					padding-right: 0;
					p {
						max-width: 560px;
						margin: 0 0 42px;
						color: #d0c9c9;
						@media #{$md}{
							max-width: 500px;
						}
						@media #{$sm}{
							max-width: 400px;
						}
						@media #{$xs}{
							max-width: 350px;
						}
					}
				}
				// &__btn{
				// 	&-wrap{
				// 		display: flex;
				// 		align-items: center;
				// 		justify-content: start;
				// 		gap: 20px 30px;
				// 	}
				// }
				&__shape{
					&-one{
						position: absolute;
						top: 0;
						left: -205px;
						z-index: 1;
						animation: bdtranslate 2s ease-in-out 1s forwards infinite alternate;
						@media #{$xxl,$xl,$lg}{
							max-width: 60%;
						}
						@media #{$md}{
							max-width: 70%;
						}
						@media #{$sm}{
							max-width: 90%;
						}
						@media #{$xs}{
							max-width: calc(100% + 50px);
						}
						@media #{$xxs}{
							display: none;
						}
					}
					&-two{
						position: absolute;
						bottom: 0;
						right: -30px;
						z-index: 1;
						left: auto;
						transform: rotate(0deg);
						animation: bdtranslate 2s ease-in-out 1s forwards infinite alternate;
						@media #{$xl}{
							max-width: 60%;
						}
						@media #{$lg}{
							max-width: 50%;
						}
						@media #{$md}{
							max-width: 48%;
						}
						@media #{$sm}{
							max-width: 40%;
						}
						@media #{$xs}{
							max-width: 70%;
						}
					}
					&-three{
						position: absolute;
						bottom: 0;
						left: -35px;
						z-index: 1;
						animation: bdSpinSmall 6s infinite;
						@media #{$xxl}{
							max-width: 38%;
						}
						@media #{$xl}{
							max-width: 35%;
						}
						@media #{$lg}{
							max-width: 50%;
						}
						@media #{$md,$sm}{
							max-width: 60%;
						}
						@media #{$xs}{
							display: none;
						}
					}
				}
			}
		}
		&.style-five{
			.banner{
				&__content{
					padding-right: 0;
					p{
						max-width: 490px;
					}
				}
				// &__btn{
				// 	&-wrap{
				// 		display: flex;
				// 		align-items: center;
				// 		gap: 20px 30px;
				// 	}
				// }
				&__thumb{
					position: relative;
					margin-left: 15px;
					@media #{$md}{
						max-width: 500px;
						margin: 0 auto;
					}
					@media #{$sm}{
						max-width: 450px;
						margin: 0 auto;
					}
					img{
						@media #{$md,$sm,$xs}{
							width: 100%;
						}
					}
					.banner__border-anim{
						border-radius: 65% 35% 40% 60% / 59% 48% 52% 41% ;
						-webkit-animation: img-border 5s linear infinite alternate;
						-moz-animation: img-border 5s linear infinite alternate;
						-o-animation: img-border 5s linear infinite alternate;
						animation: img-border 5s linear infinite alternate;
						transition: all 0.3s ease-out 0s;
					}
					&::before{
						position: absolute;
						content: "";
						width: 100%;
						height: 100%;
						background: rgba(66, 134, 244, 0.75);;
						border-radius: 65% 35% 40% 60%/59% 48% 52% 41%;
						z-index: -1;
						left: -15px;
						right: 0;
						-webkit-animation: img-border 5s linear infinite alternate;
						-moz-animation: img-border 5s linear infinite alternate;
						-o-animation: img-border 5s linear infinite alternate;
						animation: img-border 5s linear infinite alternate;
					}
					.banner__thumb-shape{
						position: absolute;
						right: -40px;
						z-index: -1;
						top: -15px;
						@media #{$xs}{
							display: none;
						}
						@media #{$lg}{
							max-width: 130px;
						}
						@media #{$md,$sm}{
							max-width: 150px;
						}
						img{
							width: 100%;
						}
					}

				}
				&__shape{
					&-one{
						position: absolute;
						left: 100px;
						top: 42%;
						animation: zoom 5s infinite;
						@media #{$xxl}{
							left: 30px;
						}
						@media #{$xl}{
							left: 20px;
						}
						@media #{$lg}{
							left: 10px;
						}
						@media #{$md}{
							left: 50px;
  							top: 45%;
						}
						@media #{$sm}{
							left: 100px;
  							top: 45%;
						}
						@media #{$xs}{
							display: none;
						}
					}
					&-two{
						position: absolute;
						left: 80px;
						-moz-animation: rotate-infinite 3s ease-in-out 1s forwards infinite alternate;
						-webkit-animation: rotate-infinite 3s ease-in-out 1s forwards infinite alternate;
						animation: rotate-infinite 3s ease-in-out 1s forwards infinite alternate;
						@media #{$xxl,$xl}{
							left: 40px;
							bottom: 50px;
						}
						@media #{$lg}{
							bottom: 10px;
						}
						@media #{$md}{
							left: 20px;
						}
						@media #{$sm}{
							left: 20px;
							bottom: 90px;
						}
						@media #{$xs}{
							left: 20px;
							bottom: 30px;
						}
					}
					&-three{
						position: absolute;
						right: 160px;
						top: 23%;
						animation: zoom 5s infinite;
						@media #{$xxl}{
							right: 60px;
						}
						@media #{$xl}{
							right: 30px;
						}
						@media #{$xs}{
							right: 3px;
   							 top: 21%;
						}
					}
					&-four{
						position: absolute;
						bottom:66px;
						right: 160px;
						animation: zoom 5s infinite;
						@media #{$xl}{
							bottom: 40px;
						}
						@media #{$md}{
							right: 60px;
						}
						@media #{$xs}{
							bottom: 66px;
  							right: 30px;
						}
					}
				}
			}
		}
		&.style-six{
			.banner-bg{
				position: absolute;
				content: "";
				width: 42%;
				height: 100%;
				background: linear-gradient(180deg, #4286F4 0%, #5E20DD 100%);
				clip-path: polygon(25% 0%, 100% 0%, 100% 100%, 25% 100%, 0% 50%);
				right: 10px;
				left: auto;
				top: 0;
				z-index:3;
				@media #{$md,$sm}{
					width: 100%;
					height: 600px;
					left: 0px;
					bottom: 0;
					top: unset;
					right: auto;
				}
				@media #{$xs}{
					width: 100%;
					height: 500px;
					left: 0px;
					bottom: 0;
					top: unset;
					right: auto;
				}
				@media #{$xxs}{
					width: 100%;
					height: 400px;
					left: 0px;
					bottom: 0;
					top: unset;
					right: auto;
				}
			}
			.banner{
				&__content{
					padding-right: 0;
					p{
						max-width: 490px;
					}
				}
				&__thumb{
					position: absolute;
					z-index: 5;
					width: 42%;
					left: auto;
					right: 0;
					clip-path: polygon(25% 0%, 100% 0%, 100% 100%, 25% 100%, 0% 50%);
					@media #{$xxl,$xl,$md}{
						background-position: right;
					}
					@media #{$lg}{
						background-position:-160px, right;
					}
					@media #{$md,$sm}{
						position: relative;
						height: 600px;
						width: 100%;
						margin-left: 10px;
					}
					@media #{$xs}{
						position: relative;
						height: 500px;
						width: 100%;
						margin-left: 10px;
					}
					@media #{$xxs}{
						position: relative;
						height: 400px;
						width: 100%;
						margin-left: 10px;
					}
					img{
						border-radius: 0;
						clip-path: polygon(25% 0%, 100% 0%, 100% 100%, 25% 100%, 0% 50%);
					}
					&-shape{
						position: absolute;
						left:58%;
						top: 0px;
						z-index: 1;
						-moz-animation: bdtranslate 3s ease-in-out 1s forwards infinite alternate;
						-webkit-animation: bdtranslate 3s ease-in-out 1s forwards infinite alternate;
						animation: bdtranslate 3s ease-in-out 1s forwards infinite alternate;
						@media #{$xl}{
							img{
								max-width: 180px;
							}
						}
						@media #{$lg}{
							img{
								max-width: 165px;
							}
						}
						@media #{$md,$sm,$xs}{
							display: none;
						}
					}
				}
				// &__btn{
				// 	&-wrap{
				// 		display: flex;
				// 		align-items: center;
				// 		gap: 20px 30px;
				// 	}
				// }
			}
			.brand__area{
				transform: translateY(100px) translateX(-30px);
				@media #{$md,$sm,$xs}{
					transform: translateY(0px) translateX(-30px);
					margin:50px 0;
				}
			}
		}
		&.style-seven{
			.is-overlay{
				-webkit-transform: scale(1);
				-moz-transform: scale(1);
				transform: scale(1);
				-webkit-transition: all 8s linear 0s;
				-moz-transition: all 8s linear 0s;
				-o-transition: all 8s linear 0s;
				transition: all 8s linear 0s;
				&::before{
					position: absolute;
					content: "";
					width: 100%;
					height: 100%;
					background: rgba(16, 17, 20, 0.75);
					z-index: 1;
					top: 0;
					left: 0;
				}
			}
			.banner{
				&__active{
					.swiper-slide-active{
						animation-name: fadeInUp;
						animation-fill-mode: both;
					}
				}
				&__single-slide{
					min-height: 940px;
					display: flex;
					align-items: center;
					@media #{$xxl}{
						min-height: 800px;
					}
					@media #{$xl}{
						min-height: 700px;
					}
					@media #{$lg}{
						min-height: 650px;
					}
					@media #{$md,$sm}{
						min-height: 600px;
					}
					@media #{$xs}{
						min-height: 550px;
						padding-top: 140px;
						padding-bottom: 150px;
					}
				}
				&__content{
					padding-right: 0;
					padding-bottom: 0px;
					padding-top: 0px;
					p{
						max-width: 540px;
						margin: 0 auto 42px;
					}
				}
			}
			.swiper-slide-active{
				.bg__thumb-position{
					-webkit-transform: scale(1.2);
					-moz-transform: scale(1.2);
					transform: scale(1.2);
				}
			}
		}
		&.style-eight{
			.is-overlay{
				clip-path: polygon(0 1%, 100% 0, 100% 87%, 0% 100%);
				@media #{$md,$sm,$xs}{
					clip-path: polygon(0 1%, 100% 0, 100% 97%, 0% 100%);
				}
				@media #{$xxs}{
					clip-path: polygon(0 1%, 100% 0, 100% 98%, 0% 100%);
				}
				&::before{
					position:absolute;
					content: "";
					width: 100%;
					height: 100%;
					background:rgba(0, 0, 0, .70);
					z-index: 1;
					clip-path: polygon(0 1%, 100% 0, 100% 87%, 0% 100%);
					@media #{$md,$sm,$xs}{
						clip-path: polygon(0 1%, 100% 0, 100% 97%, 0% 100%);
					}
					@media #{$xxs}{
						clip-path: polygon(0 1%, 100% 0, 100% 98%, 0% 100%);
					}
					}
			}
			.banner{
				&__content{
					padding-right: 0;
					p{
						max-width: 610px;
					}
				}
				&__title{
					span{
						display: inline-block;
						@media #{$md,$sm,$xs}{
							display: unset;
						}
					}
				}
				&__thumb{
					max-width: 500px;
					@media #{$xxl}{
						transform: translateY(-70px);
						max-width: 100%;
					}
					@media #{$xl}{
						transform: translateY(-50px);
						max-width: 100%;
					}
					@media #{$lg}{
						transform: translateY(-20px);
						max-width: 100%;
					}
					@media #{$md}{
						max-width: 500px;
						margin: 0 auto;
					}
					@media #{$sm,$xs}{
						max-width: 100%;
					}
					img{
						border-radius: 0;
						clip-path: polygon(0 1%, 100% 0, 100% 96%, 0% 100%);
						@media #{$md,$sm,$xs}{
							clip-path: polygon(0 1%, 100% 0, 100% 96%, 0% 99%);
						}
					}
					&-wrapper{
						margin: 0;
					}
				}
			}
		}
	}
	&__btn{
		&-wrap{
			display: flex;
			align-items: center;
			gap: 20px 20px;
			flex-wrap: wrap;
		}
	}
	&__navigation{
		button{
			top: 50%;
			position: absolute;
			transform: translateY(-50%);
			z-index: 2;
			left: 40px;
			&.slider__button-next{
				left: auto;
				  right: 40px;
			}
		}
	}
}

// mouse-scroll
.mouse{
	&__scroll{
		position: absolute;
		bottom: 30px;
		left: 50%;
		-webkit-transform: translateX(-50%);
		-moz-transform: translateX(-50%);
		-ms-transform: translateX(-50%);
		-o-transform: translateX(-50%);
		transform: translateX(-50%);
		z-index: 1;
		&::before{
			position: absolute;
			content: "";
			top: 8px;
			left: 9px;
			width: 6px;
			height: 6px;
			background: var(--bd-theme-primary);
			border-radius: 50%;
			animation: scroll 1s infinite ease-in-out alternate;
			-webkit-animation: scroll 1s infinite ease-in-out alternate;
		}
		&-btn{
			display: inline-block;
			width: 24px;
			height: 40px;
			border: 2px solid var(--bd-border-primary);
			border-radius: 30px;
			outline: none;
			&:hover{
				border-color: var(--bd-theme-primary);
			}
		}
	}
}

// banner-title-anim
.banner-title-anim {
	-webkit-background-clip: text;
	-webkit-text-fill-color: transparent;
	background-clip: text;
	text-fill-color: transparent;
	-webkit-text-fill-color: transparent;
	background-image: --webkit-linear-gradient(291.09deg, #4286F4 27.31%, #fff 73.17%);
	background-image: -moz-linear-gradient(291.09deg, #4286F4 27.31%, #fff 73.17%);
	background-image: -ms-linear-gradient(291.09deg, #4286F4 27.31%, #fff 73.17%);
	background-image: -o-linear-gradient(291.09deg, #4286F4 27.31%, #fff 73.17%);
	background-image: linear-gradient(291.09deg, #4286F4 27.31%, #fff 73.17%);
	-moz-animation: bdBannerTitleAnim 2.5s ease-in-out 1s forwards infinite alternate;
	-webkit-animation: bdBannerTitleAnim 2.5s ease-in-out 1s forwards infinite alternate;
	animation: bdBannerTitleAnim 1s ease-in-out 2.5s forwards infinite alternate;
	background-size: 150% 150%;
}

