@use '../../utils' as *;

/*----------------------------------------*/
/*  7.43 portfolio css
/*----------------------------------------*/

.portfolio {
	&__hover{
		&-wrapper{
			background-color: var(--bd-common-white);
			padding: 25px 27px 20px;
			border-radius: 8px;
		}
		&-title{
			color: rgba(15, 15, 15, .8);
		}
		&-category{
			color:rgba(15, 15, 15, 0.67)
		}
	}
	&__wrapper {
		&.style-three {
			.portfolio__active-two {
				margin-right: -410px;
				@media #{$xl,$lg,$sm} {
					margin-right: -300px;
				}
				@media #{$xs} {
					margin-right: 0;
				}
			}
		}
		&.style-four {
			.portfolio__active {
				overflow: visible;
			}
			.swiper-slide-prev {
				margin: 0 85px;
				margin: 0 85px;
				@media #{$lg,$md,$sm,$xs}{
					margin: 0;
				}
			}
		}
		&.style-six {
			position: relative;
			&.portfolio{
				&-details{
					.swiper-slide-prev{
						margin: 0;
					}
					.portfolio{
						&__navigation{
							button{
								&.circle-btn{
									width: 80px;
									height: 80px;
									font-size: 20px;
									left: 90px;
									@media #{$lg}{
										left: -10px;
									}
									@media #{$md}{
										width: 60px;
										height: 60px;
										font-size: 16px;
										left: 40px;
									}
									@media #{$sm}{
										width: 60px;
										height: 60px;
										font-size: 16px;
										left: 0px;
									}
								}
								&.portfolio__button-next {
									left: auto;
									right: 90px;
									@media #{$lg}{
										right: -10px;
									}
									@media #{$md}{
										right: 40px;
									}
									@media #{$sm}{
										right: 0px;
									}
								}
							}
						}
					}
				}
			}
			.portfolio__active {
				overflow: visible;
			}
			.portfolio-details__active {
				overflow: visible;
			}
			.swiper-slide-prev {
				margin: 0 85px;
				@media #{$lg,$md,$sm,$xs}{
					margin: 0;
				}
			}
			.portfolio {
				&__navigation {
					position: unset;
					button {
						top: 50%;
						position: absolute;
						transform: translateY(-50%);
						z-index: 2;
						left: 40px;
						@media #{$lg}{
							left: 230px;
						}
						@media #{$md}{
							left: 110px;
						}
						@media #{$sm}{
							left: -40px;
						}
						&.circle-btn {
							width: 60px;
							height: 60px;
							font-size: 18px;
							border: none;
							@media #{$md,$sm}{
								width: 50px;
								height: 50px;
								font-size: 15px;
							}
						}
						&.portfolio__button-next {
							left: auto;
							right: 40px;
							@media #{$lg}{
								right: 230px;
							}
							@media #{$md}{
								right: 110px;
							}
							@media #{$sm}{
								right: -40px;
							}
						}

					}
				}
			}
		}
	}
	&__item {
		position: relative;
		border-radius: 8px;
		overflow: hidden;
		&.style-two {
			padding: 0;
			border-radius: 0;
			.portfolio {
				&__item {
					&-thumb {
						img {
							border-radius: 8px;
							width: 100%;
						}
						&::before {
							position: absolute;
							content: "";
							left: 0;
							top: 0;
							width: 100%;
							height: 100%;
							background: linear-gradient(0deg, rgba(66, 134, 244, 0.90) 0%, rgba(66, 134, 244, 0.00) 100%);
							opacity: 0;
							transition: .3s;
							border-radius: 8px;
							visibility: hidden;
						}
					}
					&-content {
						position: absolute;
						bottom: 0px;
						left: 40px;
						right: 40px;
						z-index: 1;
						padding: 0;
						transform: translateY(0);
						visibility: hidden;
						transition: all 0.3s ease-out 0s;
						border-radius: none;
						opacity: 0;
						@media #{$xxs}{
							left: 25px;
							right: 25px;
						}
						&:before {
							content: none;
						}
					}
					&-title {
						margin-bottom: 15px;
						color: var(--bd-common-white);
					}
				}
				&__tag {
					color: var(--bd-common-white);

					&::before {
						background: var(--bd-common-white);
					}
				}
				&__btn {
					.bd-btn {
						margin-top: 50px;
						display: block;
					}
				}
			}
			&:hover {
				.portfolio__item-content {
					opacity: 1;
					visibility: visible;
					bottom: 40px;
				}
				.portfolio__item-thumb {
					&::before {
						opacity: 1;
						visibility: visible;
					}
				}
			}
		}
		&.style-three {
			padding: 0;
			position: relative;
			z-index: 1;
			min-height: 500px;
			overflow: hidden;
			.portfolio {
				&__item {
					&-thumb {
						position: absolute;
						top: 0;
						left: 0;
						width: 100%;
						height: 100%;
						z-index: -1;
						background-position: center;
						background-size: cover;
						background-repeat: no-repeat;
						&::before {
							position: absolute;
							content: "";
							left: 0;
							top: 0;
							width: 100%;
							height: 100%;
							background: linear-gradient(0.53deg, rgba(66, 134, 244, 1.00) 0%, rgba(95, 29, 220, 1.00) 100%);
							transition: opacity 0.3s ease-in-out;
							border-radius: 8px;
							opacity: 0;
							visibility: hidden;
						}
					}
					&-content {
						position: absolute;
						bottom: 0;
						left: 40px;
						right: 40px;
						z-index: 1;
						padding: 0;
						transform: translateY(0);
						transition: all 0.3s ease-out 0s;
						border-radius: none;
						opacity: 0;
						visibility: hidden;
						@media #{$xl,$xxs} {
							left: 25px;
						}
					}
					&-btn {
						position: absolute;
						top: 50%;
						left: 50%;
						transform: translate(-50%, -50%) scale(0.3);
						visibility: hidden;
						opacity: 0;
						transition: all 0.4s ease-out;
					}
				}
				&__tag {
					background: var(--bd-common-white);
					color: var(--bd-common-black);
					padding: 0px 10px;
					font-size: 16px;
					margin-bottom: 15px;
					height: 30px;
					display: inline-flex;
					align-items: center;
					justify-content: center;
					filter: drop-shadow(0px 4px 10px rgba(0, 0, 0, 0.25));
					&::before {
						content: none;
					}
					&:hover {
						background: var(--bd-common-black);
						color: var(--bd-common-white);
					}
				}
			}
			&:hover {
				.portfolio {
					&__item {
						&-btn {
							transform: translate(-50%, -50%) scale(1);
							visibility: visible;
							opacity: 1;
						}
						&-content {
							bottom: 40px;
							opacity: 1;
							visibility: visible;
						}
						&-thumb {
							&::before {
								visibility: visible;
								opacity: .8;
							}
						}
					}
				}
			}
		}
