							position: absolute;
							content: "";
							left: 20px;
							top: 20px;
							width: calc(100% - 40px);
							height: calc(100% - 40px);
							background: var(--bd-rgba-4);
							transition: all 0.3s ease-out 0s;
							border-radius: 8px;
							opacity: 0;
							visibility: hidden;
							transform: perspective(600px) rotateY(20deg);
						}
					}
					&-content {
						position: absolute;
						left: 60px;
						right: 60px;
						bottom: 55px;
						z-index: 1;
						transition: all 0.3s ease-out 0s;
						@media #{$xxs}{
							left: 40px;
							right: 40px;
							bottom: 40px;
						}
