	&__thumb {
		position: relative;
		text-align: center;
		img{
			border-radius: 8px;
			width: 100%;
		}
	}
	&__content {
		position: absolute;
		height: auto;
		bottom: 30px;
		left: 30px;
		right: 30px;
		z-index: 1;
		padding: 20px 30px 23px;
		display: flex;
		justify-content: space-between;
		gap: 0 40px;
		align-items: center;
		transform: translateY(100%);
		opacity: 0;
		visibility: hidden;
		transition: all 0.3s ease-out 0s;
		border-radius: 8px;
		overflow: hidden;
		@media #{$xl,$lg,$md,$sm,$xs,$xxs} {
			flex-wrap: wrap;
			gap: 15px 15px;
			padding: 15px 25px 20px;
		}
		&::before {
			position: absolute;
			content: "";
			width: 100%;
			height: 100%;
			top: 0;
			left: 0;
			background: var(--bd-bg-primary);
			z-index: -1;
		}
	}
	&__title {
		a {
			&:hover {
				color: var(--bd-theme-primary);
			}
		}
	}
	&__tag {
		position: relative;
		color: var(--bd-theme-primary);
		font-size: 15px;
		font-weight: 500;
		margin-bottom: 8px;
		display: inline-block;
		padding-left: 8px;
		transition: all 0.3s ease-out 0s;
		border-radius: 4px;
		&::before {
			position: absolute;
			content: "";
			width: 2px;
			height: calc(100% - 12px);
			background: var(--bd-theme-primary);
			top: 50%;
			left: 0;
			transform: translateY(-50%);
		}
	}
	&__navigation {
		position: absolute;
		left: 170px;
		z-index: 5;
		bottom: 0;
		@media #{$md}{
			left: 130px;
		}
	}
	&__details{
		&-meta{
			display: grid;
			grid-template-columns: repeat(2,1fr);
			gap:40px 20px;
			margin-left: 50px;
			@media #{$md,$sm,$xs}{
				margin-left: 0;
			}
			@media #{$xxs}{
				grid-template-columns: repeat(1,1fr);
				gap:30px 20px;
			}
			&-item{
				display: flex;
				gap: 10px;
			}
		}
		&-sec-wrapper{
			margin-right: 40px;
			@media #{$md,$sm,$xs}{
				margin-right: 0;
			}
			p{
					margin-bottom: 30px;
			}
		}
		&-info-wrapper{
			position: sticky;
  			top: 120px;
			.portfolio{
				&__details{
					&-sec-wrapper{
						margin-right: 0;
					}
				}
			}
			.portfolio__details-meta{
				margin-left: 0;
				margin-bottom: 40px;
			}
		}
		&-img-list{
			margin-right: 40px;
			@media #{$md,$sm,$xs}{
				margin-right: 0;
			}
			img{
				width: 100%;
				border-radius: 8px;
			}
			&-box{
				&:not(:last-child){
					margin-bottom: 10px;
				}
			}
		}
		&-video{
			&-wrapper{
				position: relative;
				.video__play{
					position: absolute;
					left: 0;
					right: 0;
					top: 50%;
					transform: translateY(-50%);
					z-index: 3;
					margin-bottom: 0;
				}
			}
			&-thumb{
				position: relative;
				&::before{
					position: absolute;
					content: "";
					width: 100%;
					height: 100%;
					background: var(--bd-common-black);
					opacity: .3;
				}
				img{
					border-radius: 8px;
					width: 100%;
				}
			}
		}
		&-image-item{
			img{
				width: 100%;
				border-radius: 8px;
			}
		}
	}
}

// need margin
.bd-portfolio__area {
	.is-margin {
		margin-right: 30px;

		@media #{$xl,$lg,$md,$sm,$xs} {
			margin-right: 0;
		}
	}
}
