@use '../utils' as *;

/*----------------------------------------*/
/*  2.13 List
/*----------------------------------------*/
.info__list {
	ul {
		li {
			display: flex;
			align-items: center;
			gap: 12px;
			line-height: 1.3;
			&:not(&:last-child) {
				margin-bottom: 20px;
			}
			i {
				font-size: 16px;
				color: var(--bd-theme-primary);
			}
			p {
				margin-bottom: 0;
			}
		}
	}
}

.info__list-2 {
	position: relative;
	z-index: 5;
	margin-top: 13px;
	ul {
		display: flex;
		align-items: start;
		justify-content: center;
		gap: 15px 30px;
		flex-wrap: wrap;
		li {
			display: flex;
			align-items: self-start;
			gap: 10px;
			 i {
				color: var(--bd-common-white);
				line-height: 24px;
				font-weight: 700;
				font-size: 16px;
			}
			P {
				color: var(--bd-common-white);
				margin-bottom: 0;
			}
		}
	}
}
.info__list-3 {
	ul {
		display: flex;
		align-items: start;
		gap: 10px 25px;
		flex-wrap: wrap;
		li {
			display: flex;
			align-items: self-start;
			gap: 10px;
			P {
				margin-bottom: 0;
			}
		}
	}
}
.bd {
	&__feature-list {
		ul {
			li {
				@include flexbox();
				gap: 12px;
				&:not(:last-child) {
				  margin-bottom: 25px;
				}
				span {
				  width: 22px;
				  height: 22px;
				  min-width: 22px;
				  background: rgba($color: $theme-primary, $alpha: .1);
				  display: inline-flex;
				  justify-content: center;
				  align-items: center;
				  font-size: 16px;
				  border-radius: 50%;
				  color: var(--bd-theme-primary);
				  position: relative;
				  top: 3px;
				}
				p {
					margin-bottom: 0;
				}
			}
		}
	}
}