@use '../utils' as *;

/*----------------------------------------*/
/*  2.2 forms css
/*----------------------------------------*/


// form stye start
.from__input-box {
	&.has-icon {
		.form__input {
			position: relative;
			span {
				position: absolute;
				display: inline-block;
			}
		}
		&.icon-left {
			.form__input {
				input,
				textarea {
					padding-left: 45px;
				}
			}
		}
		&.icon-right {
			.form__input {

				input,
				textarea {
					padding-right: 45px;
				}
			}
		}
	}
}
.from__input-box {
	&.has-icon {
		&.icon-left {
			.form__input {
				span {
					left: 20px;
					top: 22px;
				}
			}
		}
		&.icon-right {
			.form__input {
				span {
					right: 20px;
					top: 22px;
				}
			}
		}
	}
	.form__input {
		span {
			// display: none;
		}
	}
}
.from__input-box {
	&.label-hidden {
		label {
			display: none;
		}
	}
}
.form {
	&__input {
		position: relative;
		&-title {
			label {
				margin-bottom: 5px;
				text-transform: capitalize;
				font-size: 16px;
				text-align: left;
				display: block;

				span {
					color: var(--bd-theme-primary);
				}
			}
		}
	}
	&__input {
		textarea {
			height: 220px;
			padding: 22px 20px;
			resize: none;
		}
	}
	&__select {
		&.nice-select {
			height: 60px;
			width: 100%;
			background: rgba(255, 255, 255, 0.05);
			padding: 0 20px;
			display: flex;
			align-items: center;
			float: none;
			border: 1px solid rgba(255, 255, 255, 0.1);
			@include light {
				background: rgba(235, 235, 235, 0.9);
			}
			&.open {
				font-size: 16px;

				.list {
					@include transform(scale(1) translateY(0px));
					box-shadow: 0 0 0 1px rgb(56, 56, 56)
				}
			}
			.current {
				text-align: left;
				font-size: 16px;
				position: relative;
				@include light {
					color: var(--bd-common-placeholder);
				}
			}
			.list {
				transform-origin: center center;
				@include transform(scale(0.9) translateY(0px));
				width: 100%;
				padding: 10px 0;
				background: var(--bd-bg-quaternary);

				@include light {
					background: rgb(236, 236, 236);
				}
			}
			&::after {
				color: var(--bd-text-body);
				font-size: 18px;
				right: 23px;
				width: 8px;
				height: 8px;
			}
			.option {
				font-size: 16px;
				&:hover {
					background-color: var(--bd-theme-primary);
					  color: var(--bd-common-white);
				}
			}
			.option.focus {
				background-color: var(--bd-theme-primary);
  				color: var(--bd-common-white);
			}
			.option.selected.focus {
				background-color: var(--bd-theme-primary);
				  color: var(--bd-common-white);
			}
		}
	}
}
.from__input-box {
	&.from-flex {
		display: flex;
		gap: 0 10px;

		@media #{$xs} {
			flex-direction: column;
		}
	}
}
.form__input-title {
	width: 120px;
}
.form__input {
	position: relative;
	flex-grow: 14;
}

.form__input{
	&.style-two{
		input{
			border: 1px solid #4a4a4a;
			background: #121314;
		}
		textarea{
			border: 1px solid #4a4a4a;
			background: #121314;
		}
		input,textarea{
			&:focus{
				border-color: var(--bd-theme-primary);
			}
		}
	}
	&.style-three{
		input{
			border: 1.5px solid #4A4A4A;
			background: rgba(29, 30, 31, 0.70);
		}
		textarea{
			border: 1.5px solid #4A4A4A;
			background: rgba(29, 30, 31, 0.70);
		}
		input,textarea{
			&:focus{
				border-color: var(--bd-theme-primary);
			}
		}
	}
}

// Floating form
.floating__form-input {
	&.floating-bg{
		input{
			&:focus {
				~ {
					.floating-label {
						background-color: var(--bd-bg-primary);
					}
				}
			}
		}
	}
	input {
		border-color: var(--bd-border-secondary);
		&:focus {
			~ {
				.floating-label {
					top: 0;
					left: 16px;
					font-size: 16px;
					font-weight: 400;
					background-color: var(--bd-bg-secondary);
					padding: 0px 4px;
					color: var(--bd-theme-primary);
				}
			}
		}
		&:not(:focus) {
			&:valid {
				~ {
					.floating-label {
						top: 0;
						left: 20px;
						font-size: 16px;
						font-weight: 400;
						padding: 0px 4px;
						color: var(--bd-theme-primary);
						background-color: var(--bd-bg-secondary);
					}
				}
			}
		}
		background-color: transparent;
	}
	.floating-label {
		position: absolute;
		pointer-events: none;
		left: 20px;
		top: 50%;
		transform: translateY(-50%);
		transition: 0.2s ease all;
	}
	position: relative;
	textarea {
		height: 200px;
		padding: 22px 20px;
		resize: none;
		background: transparent;
		&:focus {
			~ {
				.floating-label-two {
					top: -12px;
					left: 15px;
					padding: 0px 4px;
					background-color: var(--bd-bg-secondary);
					color: var(--bd-theme-primary);
				}
			}
		}
		&:not(:focus) {
			&:valid {
				~ {
					.floating-label-two {
						top: -12px;
						left: 20px;
						padding: 0px 4px;
					}
				}
			}
		}
	}
	.floating-label-two {
		position: absolute;
		pointer-events: none;
		left: 20px;
		top: 20px;
		transition: 0.2s ease all;
		;
	}
}
.floating {
	&__form-content {
		padding-right: 60px;
		@media #{$lg}{
			padding-right: 40px;
		  }
		@media #{$md}{
			padding-right: 20px;
		  }
		@media #{$sm,$xs}{
			padding-right: 0;
		  }
	}
}
// btn
.contact {
	&__btn {
		margin-top: 20px;
		.bd-btn {
			border-radius: 8px;
		}
	}
}
// form content
.contact {
	&__info {
		&-wrapper{
			max-width: 503px;
			margin: 0 auto;
			padding-top: 75px;
			@media #{$sm,$xs}{
				max-width: 100%;
				padding-top: 0;
			}
		}
		h6 {
			font-weight: 500;
			color: var(--bd-text-secondary);
			a {
				&:hover {
					color: var(--bd-theme-primary);
				}
			}
		}
	}
	&__thumb {
		margin-bottom: 30px;
		img{
			border-radius: 8px;
			width: 100%;
		}
	}
}
.contact__thumb {
	margin-bottom: 30px;
}
// Custom checkbox button
input[type="checkbox"] {
	display: none;
	&:checked {
		~ {
			label {
				&::before {
					-webkit-transform: scale(1);
					-moz-transform: scale(1);
					-ms-transform: scale(1);
					-o-transform: scale(1);
					transform: scale(1);
				}
			}
		}
	}
	~label {
		position: relative;
		padding-left: 30px;

		&::after,
		&::before {
			position: absolute;
			content: "";
			left: 0;
			top: 2px;
			width: 20px;
			height: 20px;
			background-color: rgba(255, 255, 255, 0.05);
			border: 1px solid var(--bd-border-primary);
			border-radius: 4px;
			transition: .2s;
		}

		&::before {
			width: 12px;
			height: 12px;
			background-color: var(--bd-theme-primary);
			left: 4px;
			top: 6px;
			right: 3px;
			bottom: 3px;
			@include transform(scale(0));
		}

		&:hover {
			cursor: pointer;
		}
	}
}
// Custom radio button 
input[type="radio"] {
	opacity: 0;
	position: absolute;
	~label {
		position: relative;
		font-size: 15px;
		line-height: 25px;
		font-weight: 400;
		padding-left: 28px;
		cursor: pointer;
		margin-bottom: 0;
		&::before {
			content: "";
			position: absolute;
			top: 3px;
			left: 0;
			width: 20px;
			height: 20px;
			background-color: transparent;
			border: 1px solid var(--bd-border-primary);
			border-radius: 2px;
			transition: all 0.3s;
		}
		&::after {
			content: " ";
			position: absolute;
			top: 9px;
			left: 2px;
			width: 10px;
			height: 5px;
			background-color: transparent;
			border-radius: 2px;
			transform: rotate(-45deg);
			opacity: 0;
			transition: all .3s;
		}
	}
	&:checked {
		~label {
			&::before {
				border-color: var(--bd-theme-primary);
			}

			&::after {
				opacity: 1;
				background-color: var(--bd-theme-primary);
			}
		}
	}
}
input[type="radio"] {
	~label {
		&::before {
			border-radius: 50%;
		}
		&::after {
			width: 12px;
			height: 12px;
			left: 4px;
			background: #fff;
			border-radius: 50%;
			top: 7px;
		}
	}
}
// Custom switch button 
.switch-checkbox {
	position: relative;
	display: inline-block;
	width: 38px;
	height: 22px;
	input {
		opacity: 0;
		width: 0;
		height: 0;
	}
}
.switch.round {
	border-radius: 34px;
	&:before {
		border-radius: 50%;
	}
}
.switch {
	position: absolute;
	cursor: pointer;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	background-color: transparent;
	-webkit-transition: 0.4s;
	transition: 0.4s;
	border: 1px solid var(--bd-border-primary);
	&:before {
		position: absolute;
		content: "";
		height: 14px;
		width: 14px;
		left: 3px;
		bottom: 3px;
		background-color: #4B4E52;
		-webkit-transition: 0.4s;
		transition: 0.4s;
	}
}
input {
	&:checked {
		+ {
			.switch {
				background-color: #096BD8;
				border-color: #096BD8;
				&:before {
					transform: translateX(16px);
					background-color: var(--bd-common-white);
				}
			}
		}
	}
}