@use "../../utils" as *;

/*----------------------------------------*/
/*  7.35 Team css
/*----------------------------------------*/

// team css start here
.team {
  &__item {
    position: relative;
    &:hover {
      // .team__social {
      //   opacity: 1;
      //   visibility: visible;
      //   transform: translate(-50%, -50%);
      // }
      .team__thumb {
        &::before {
          opacity: 1;
        }
        img {
          transform: scale(1.02);
        }
      }
      .team__social {
        opacity: 1;
        visibility: visible;
        ul {
          li {
            transform: translateX(50px);
            &:nth-child(1) {
              transition-delay: 0s;
            }
            &:nth-child(2) {
              transition-delay: .1s;
            }
            &:nth-child(3) {
              transition-delay: .12s;
            }
            &:nth-child(4) {
              transition-delay: .14s;
            }
          }
        }
      }
    }
    &.style-two {
      &:hover {
        .team {
          &__content {
            transform: translateY(-60px);
          }
        }
      }
      .team {
        &__content {
          background: #22272C;
          box-shadow: 4px 4px 10px 0px rgba(0, 0, 0, 0.25), -4px -4px 10px 0px rgba(0, 0, 0, 0.25);
          padding: 20px 0 25px 0;
          transition: .5s;
          position: relative;
          z-index: 1;
          margin: 0 20px;
          border-radius: 8px;
        }
        &__social {
          position: unset;
          opacity: 1;
          visibility: visible;
          margin-top: 15px;
          ul {
            flex-direction: row;
            li {
              transform: translateX(0);
            }
          }
        }
      }
    }
    &.style-three {
      .team {
        &__social {
          bottom: 80px;
          top: unset;
          left: 50%;
          transform: translate(-50%, -50%);
          ul {
            flex-direction: row;
            li {
              transform: translateX(0) translateY(100%);
            }
          }
        }
      }
      &:hover {
        .team {
          &__social {
            ul {
              li {
                transform: translate(0);
              }
            }
          }
        }
      }
    }
    &.style-four {
      &:hover {
        .team {
          &__content {
            background: #22272C;
            box-shadow: 4px 4px 10px 0px rgba(0, 0, 0, 0.25), -4px -4px 10px 0px rgba(0, 0, 0, 0.25);
          }
        }
      }
      .team {
        &__content {
          transition: .5s;
          position: relative;
          z-index: 1;
          border-radius: 0px 0px 8px 8px;
          padding: 20px 0 25px 0;
        }
        &__thumb {
          border-radius: 8px 8px 0 0;
          &::before {
            border-radius: 8px 8px 0 0;
            background: linear-gradient(0deg, #4286F4 2.1%, #6409D8 100.81%);
          }
        }
        &__social {
          position: unset;
          opacity: 1;
          visibility: visible;
          margin-top: 15px;
          ul {
            flex-direction: row;
            li {
              transform: translateX(0);
            }
          }
        }
      }
    }
    &.style-five {
      .team {
        &__thumb {
          position: relative;
        }
        &__social {
          position: absolute;
          opacity: 1;
          visibility: visible;
          bottom: 0;
          top: unset;
          left: 0px;
          &::before {
            position: absolute;
            content: "";
            width: 40px;
            height: 40px;
            background: var(--bd-theme-primary);
            left: 0;
            bottom: 0;
            transition: 0.3s cubic-bezier(0.24, 0.74, 0.58, 1);
            border-radius: 8px;
          }
          &:hover {
            &::before {
              height: 205px;
            }
          }
          ul {
            gap: 0px;
            li {
              transform: translateX(0);
              &:hover {
                >.team__social-inner {
                  ul {
                    li {
                      visibility: visible;
                      opacity: 1;
                    }
                  }
                }
              }
             > a {
                // background: transparent;
                color: var(--bd-common-white);
                border-radius: 8px;
                width: 40px;
                height: 40px;
                font-size: 16px;
                color: var(--bd-theme-primary);
                &:hover{
                  background: var(--bd-common-white);
                  color: var(--bd-theme-primary);
                }
              }
              &:hover {
                >.team {
                  &__social {
                    &-inner {
                      ul {
                        li {
                          transition-delay: 0s;
                          &:nth-child(1) {
                            bottom: 41px;
                            z-index: -1;
                          }
                          &:nth-child(2){
                            bottom: 82px;
                            z-index: -1;
                          }
                          &:nth-child(3){
                            bottom: 123px;
                            z-index: -1;
                          }
                          &:nth-child(4){
                            bottom: 164px;
                            z-index: -1;
                          }
                        }
                      }
                    }
                  }
                }
              }
            }
          }
          &-inner {
            bottom: 0;
            left: -15px;
            position: absolute;
            ul{
              li {
                position: absolute;
                transition: all 0.4s ease-out 0s;
                visibility: hidden;
                opacity: 0;
                bottom: 0;
                left: 15px;
                a{
                  border-radius: 8px;
                  background: transparent;
                  color: var(--bd-common-white);
                  &:hover{
                    background: var(--bd-common-white);
                    color: var(--bd-theme-primary);
                  }
                }
              }
            }
          }
        }
      }
    }
  }
  &__thumb {
    z-index: 1;
    overflow: hidden;
    position: relative;
    border-radius: 8px;
    margin: 0;
    &.bg-solid {
      background: var(--bd-bg-tertiary);
    }
    &::before {
      position: absolute;
      content: "";
      height: 100%;
      width: 100%;
      left: 0;
      top: 0;
      z-index: -1;
      border-radius: 8px;
      background: linear-gradient(0deg, rgba(66, 134, 244, 0.70) 0.33%, rgba(66, 134, 244, 0.00) 99.73%);
      box-shadow: 4px 4px 10px 0px rgba(0, 0, 0, 0.25), -4px -4px 10px 0px rgba(0, 0, 0, 0.25);
      transition: 0.6s cubic-bezier(0.5, 1, 0.89, 1);
      opacity: 0;
    }
  }
  &__content {
    padding: 20px 0 0 0;
  }
  &__title {
    margin-bottom: 5px;
    a {
      &:hover {
        color: var(--bd-theme-primary);
      }
    }
  }
  &__social {
    position: absolute;
    left: -30px;
    top: 40px;
    z-index: 1;
    opacity: 0;
    visibility: hidden;
    transition: .4s;
    // opacity: 0;
    // visibility: hidden;
    ul {
      display: flex;
      align-items: center;
      justify-content: center;
      gap: 10px;
      flex-direction: column;

      li {
        list-style: none;
        transform: translateX(100%);
        transition: visibility 0.3s 0.1s ease-out, opacity 0.3s 0.15s ease-out, transform 0.3s 0.1s ease-out;
        a {
          width: 35px;
          height: 35px;
          background: var(--bd-common-white);
          font-size:15px;
          display: inline-flex;
          align-items: center;
          justify-content: center;
          color: var(--bd-theme-primary);
          @include border-radius(50%);
          &:hover {
            background-color: var(--bd-theme-primary);
            color: var(--bd-common-white);
          }
        }
      }
    }
  }
  &__navigation {
    display: flex;
    justify-content: end;

    button {
      i {
        position: relative;
        z-index: 2;
      }
    }
  }
  &__details{
    &-wrapper{
      border: 1px solid var(--bd-border-primary);
      padding: 50px 50px 50px 50px;
      border-radius: 8px;
      max-width: 80%;
      margin: 0 auto;
      @media #{$lg,$md,$sm,$xs}{
        max-width: 100%;
      }
      @media #{$md}{
        padding: 40px;
      }
      @media #{$sm,$xs}{
        padding: 30px;
      }
    }
    &-thumb{
      &.bg-solid{
        background: var(--bd-bg-tertiary);
        border-radius: 8px;
      }
    }
    &-content{
      padding-left: 30px;
      @media #{$md}{
        padding-left: 10px;
      }
      @media #{$sm,$xs}{
        padding-left: 0;
      }
      .team{
        &__details{
          &-author{
            margin-bottom: 30px;
          }
        }
      }
      p{
        margin-bottom: 30px;
      }
    }
  }
}

// team css end here